import React from 'react';
import classNames from 'classnames';
import './button.css';

const Button = ({ label, onClick, className, placeholder = 'Button' }) => {
  const defaultClassName = 'button';
  const buttonClassName = classNames(defaultClassName, className);

  return (
    <button className={buttonClassName} onClick={onClick}>
      {label || placeholder}
    </button>
  );
};

export default Button;
