import React from 'react';
import './textfield.css';

const TextField = ({ placeholder, value, onChange, className = '', type = 'text' }) => {
  const defaultClassName = 'textfield';
  const textFieldClassName = className ? `${defaultClassName} ${className}` : defaultClassName;

  return (
    <input
      type={type}
      className={textFieldClassName}
      placeholder={placeholder}
      value={value}
      onChange={onChange}
    />
  );
};

export default TextField;
