import React from 'react';
import classNames from 'classnames';
import './forgotpassword.css';

const ForgotPassword = ({ onClick, className = '' }) => {
  const defaultClassName = 'forgotpassword';
  const forgotPasswordClassName = classNames(defaultClassName, className);

  return (
    <div className={forgotPasswordClassName} onClick={onClick}>
      Forgot password?
    </div>
  );
};

export default ForgotPassword;
